.panel-field-description {
	opacity: .4;
	font-size: .85em;
  line-height: 1.4em;
  display: block;
}

/* Style tweaks */
.section {
	max-width: 1400px;
	margin: 0 auto;
}

.structure-entry,
.dashboard-section {
	border: none;
	border-radius: 4px;
	margin-bottom: 1.5em;
}

.structure-entry-options.builder-entry-options .btn:last-child {
	border-right: none;
}

.structure-entry-options .btn {
	padding-bottom: .85em;
}

.input {
	padding: .5em .75em .5em;
	border-radius: 4px;
	border-color: #fff;
	min-height: 40px;
}

.field-with-buttons .input {
	padding-bottom: 3em !important;
	box-sizing: border-box;
}

.input.input-with-tags {
	padding: .35em .2em 0 .35em;
}

.input.input-is-readonly {
	background: #ddd;
	border-color: #ddd;
	pointer-events: none;
}

.input.input-is-readonly:focus {
	background: #ddd;
	border-color: #ddd;
}

.structure-entry .input,
.dashboard-section .input,
.modal-content .input {
	border-color: #ddd;
}

.input:focus {
	border-color: #000;
}

.tag {
	margin: 0 5px 5px 0;
	border-radius: 20px;
}

.tag .tag-x {
	padding: .35em .85em .45em .7em
}

body.ltr .tag button.tag-label {
  font-size: 12px;
  padding: .6em 3.5em .5em 12px;
  line-height: 1em;
}

body.ltr .field-icon {
	background: transparent;
	border-left: none;
}

.field-buttons {
	background: transparent;
}

