.minicolors-panel {
	width: 190px;
	height: 160px;
	border: none;
	border-radius: 4px;
}

.minicolors .minicolors-grid {
	border-radius: 4px;
	height: 140px;
	width: 140px;
	top: 10px;
	left: 10px;
}

.minicolors-slider, .minicolors-opacity-slider {
	border-radius: 20px;
	// padding: 0 0 10px;
	left: 160px;
	top: 10px;
	height: 140px;
}

